<template>
  <section class="p-6">
    <div class="page-shell">
      <!-- Ìó§Îçî -->
      <header class="mb-2 header-row">
        <div class="header-title">
          <h2
            class="text-2xl md:text-3xl font-bold tracking-tight whitespace-nowrap align-middle"
          >
            ÏÉÅÎã¥ Î™©Î°ù
          </h2>
        </div>
      </header>

      <!-- üîç Í≤ÄÏÉâ / ÌïÑÌÑ∞ / Ï†ïÎ†¨ ÎùºÏù∏ -->
      <div class="filter-row">
        <form class="filter-form" @submit.prevent="onSearch">
          <!-- Í≤ÄÏÉâ Ïù∏Ìíã -->
          <div class="filter-field filter-field--search">
            <input
              v-model="searchText"
              type="text"
              class="search-input"
              :placeholder="searchPlaceholder"
            />
          </div>

          <!-- ÏÉÅÌÉú ÏÖÄÎ†âÌä∏ -->
          <div class="filter-field filter-field--select select-wrapper">
            <select v-model="statusFilter" class="select-input">
              <option value="ALL">Ï†ÑÏ≤¥ ÏÉÅÌÉú</option>
              <option value="TEMP">ÏÉÅÎã¥Ï†Ñ</option>
              <option value="CB3">Í≤ÄÌÜ†Ï†Ñ</option>
              <option value="CB4">Î∞òÎ†§</option>
              <option value="CB5">Í≤ÄÌÜ†ÏôÑÎ£å</option>
              <option value="CB6">Ïû¨ÏäπÏù∏ÏöîÏ≤≠</option>
            </select>
          </div>

          <!-- Ï†ïÎ†¨ ÏÖÄÎ†âÌä∏ -->
          <div class="filter-field filter-field--select select-wrapper">
            <select v-model="sortOption" class="select-input">
              <option value="COUNSEL_RECENT">ÏÉÅÎã¥Ïùº ÏµúÏã†Ïàú</option>
              <option value="COUNSEL_OLD">ÏÉÅÎã¥Ïùº Ïò§ÎûòÎêúÏàú</option>
              <option value="SUBMIT_RECENT">Ï†úÏ∂úÏùº ÏµúÏã†Ïàú</option>
              <option value="SUBMIT_OLD">Ï†úÏ∂úÏùº Ïò§ÎûòÎêúÏàú</option>
              <option value="NAME">Ïù¥Î¶ÑÏàú</option>
            </select>
          </div>

          <!-- Í≤ÄÏÉâ Î≤ÑÌäº -->
          <div class="filter-field filter-field--button">
            <MaterialButton type="submit" color="dark" size="sm">
              Í≤ÄÏÉâ
            </MaterialButton>
          </div>
        </form>
      </div>

      <!-- ÏÉÅÌÉú ÌëúÏãú -->
      <div v-if="loading" class="text-gray-500">Î∂àÎü¨Ïò§Îäî Ï§ë...</div>
      <div v-else-if="error" class="text-red-600">{{ error }}</div>
      <div v-else-if="list.length === 0" class="empty-state">
        ÏÉÅÎã¥ ÎÇ¥Ïó≠Ïù¥ ÏóÜÏäµÎãàÎã§.
      </div>

      <!-- ÏÉÅÎã¥ Î™©Î°ù Ïπ¥Îìú + ÌÖåÏù¥Î∏î -->
      <div v-else class="table-wrapper">
        <div class="table-card">
          <table class="nice-table">
            <thead>
              <tr>
                <th class="th-cell text-center w-14">No</th>

                <!-- ÏßÄÏõêÏûê Ïù¥Î¶Ñ -->
                <th class="th-cell">ÏßÄÏõêÏûê Ïù¥Î¶Ñ</th>

                <!-- Î≥¥Ìò∏Ïûê Ïù¥Î¶Ñ -->
                <th class="th-cell">Î≥¥Ìò∏Ïûê Ïù¥Î¶Ñ</th>

                <!-- Îã¥ÎãπÏûê -->
                <th class="th-cell">Îã¥ÎãπÏûê</th>

                <!-- ÏãúÏä§ÌÖúÎßå Í∏∞Í¥ÄÎ™Ö -->
                <th v-if="selectedRole === 4" class="th-cell">Í∏∞Í¥ÄÎ™Ö</th>

                <th class="th-cell">Ï°∞ÏÇ¨ÏßÄ Ï†úÏ∂úÏùº</th>
                <th class="th-cell">ÏÉÅÎã¥ ÏûëÏÑ±Ïùº</th>
                <th class="th-cell">ÏÉÅÌÉú</th>
                <th class="th-cell text-center w-24" v-if="isAssigneeRole"></th>
                <th class="th-cell text-center w-24" v-else></th>
              </tr>
            </thead>

            <tbody>
              <tr
                v-for="(row, idx) in paginatedList"
                :key="row.submit_code"
                class="table-row-item"
                @click="goDetail(row)"
              >
                <td class="td-cell text-center">
                  {{ (currentPage - 1) * pageSize + idx + 1 }}
                </td>

                <!-- ÏßÄÏõêÏûê Ïù¥Î¶Ñ: child_name ÏûàÏúºÎ©¥ ÏûêÎÖÄ Ïù¥Î¶Ñ / ÏóÜÏúºÎ©¥ Î≥∏Ïù∏ -->
                <td class="td-cell">
                  {{ row.child_name ? row.child_name : "Î≥∏Ïù∏" }}
                </td>

                <!-- Î≥¥Ìò∏ÏûêÏù¥Î¶Ñ -->
                <td class="td-cell">
                  {{ row.writer_name }}
                </td>

                <!-- Îã¥ÎãπÏûê -->
                <td class="td-cell">
                  {{ row.assi_name }}
                </td>

                <!-- ÏãúÏä§ÌÖúÎßå Í∏∞Í¥ÄÏù¥Î¶Ñ -->
                <td v-if="selectedRole === 4" class="td-cell">
                  {{ row.org_name || "-" }}
                </td>

                <td class="td-cell">{{ formatDate(row.submit_at) }}</td>

                <td class="td-cell">
                  {{
                    isTempStatus(row.status)
                      ? "-"
                      : formatDate(row.note_created_at)
                  }}
                </td>

                <!-- ÏÉÅÌÉú -->
                <td class="td-cell td-status">
                  <button
                    v-if="normStatus(row.status) === 'CB4'"
                    type="button"
                    class="status-pill status-pill--rejected status-pill--clickable"
                    @click.stop="openRejectReason(row)"
                  >
                    {{ statusLabel(row.status) }}
                  </button>

                  <span
                    v-else
                    class="status-pill"
                    :class="statusPillClass(row.status)"
                  >
                    {{ statusLabel(row.status) }}
                  </span>
                </td>

                <!-- Ïï°ÏÖò Î≤ÑÌäº -->
                <td class="td-cell text-center">
                  <template v-if="isAssigneeRole">
                    <MaterialButton
                      v-if="['CB1', 'CB2'].includes(normStatus(row.status))"
                      color="dark"
                      size="sm"
                      @click.stop="goWrite(row)"
                    >
                      ÏûëÏÑ±ÌïòÍ∏∞
                    </MaterialButton>

                    <MaterialButton
                      v-else-if="normStatus(row.status) === 'CB3'"
                      color="dark"
                      size="sm"
                      @click.stop="goEdit(row)"
                    >
                      ÏàòÏ†ïÌïòÍ∏∞
                    </MaterialButton>

                    <MaterialButton
                      v-else-if="normStatus(row.status) === 'CB4'"
                      color="dark"
                      size="sm"
                      @click.stop="goEdit(row)"
                    >
                      Ïû¨ÏàòÏ†ïÌïòÍ∏∞
                    </MaterialButton>
                  </template>
                </td>
              </tr>
            </tbody>
          </table>
        </div>

        <!-- üîπ ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò -->
        <div v-if="totalPages > 1" class="mt-6 text-center">
          <MaterialPagination color="dark" size="md" class="pagination">
            <MaterialPaginationItem
              prev
              :disabled="currentPage === 1"
              @click="changePage(currentPage - 1)"
            />
            <MaterialPaginationItem
              v-for="page in totalPages"
              :key="page"
              :label="String(page)"
              :active="page === currentPage"
              @click="changePage(page)"
            />
            <MaterialPaginationItem
              next
              :disabled="currentPage === totalPages"
              @click="changePage(currentPage + 1)"
            />
          </MaterialPagination>
        </div>
      </div>

      <!-- üîª Î∞òÎ†§ ÏÇ¨Ïú† Î™®Îã¨ -->
      <div v-if="rejectReasonModalOpen" class="modal-overlay">
        <div class="modal-container">
          <h3 class="text-lg font-semibold mb-3">Î∞òÎ†§ ÏÇ¨Ïú†</h3>

          <div v-if="rejectReasonLoading" class="text-sm text-gray-500">
            Î∂àÎü¨Ïò§Îäî Ï§ë...
          </div>

          <div v-else-if="rejectReasonError" class="text-sm text-red-600">
            {{ rejectReasonError }}
          </div>

          <div v-else class="space-y-2">
            <!-- Î∞òÎ†§ÏùºÏûê -->
            <div v-if="rejectReasonDate" class="text-xs text-gray-600">
              Î∞òÎ†§ÏùºÏûê:
              <span class="font-medium">
                {{ String(rejectReasonDate).slice(0, 10) }}
              </span>
            </div>

            <!-- Î∞òÎ†§ÏÇ¨Ïú† -->
            <div
              class="text-sm whitespace-pre-line text-gray-800 max-h-60 overflow-y-auto border rounded px-3 py-2 bg-gray-50"
            >
              {{ rejectReasonText || "Îì±Î°ùÎêú Î∞òÎ†§ ÏÇ¨Ïú†Í∞Ä ÏóÜÏäµÎãàÎã§." }}
            </div>
          </div>

          <div class="modal-actions mt-4 flex justify-end gap-2">
            <MaterialButton
              color="dark"
              size="sm"
              @click="closeRejectReasonModal"
            >
              Îã´Í∏∞
            </MaterialButton>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import MaterialButton from "@/components/MaterialButton.vue";
import MaterialPagination from "@/components/MaterialPagination.vue";
import MaterialPaginationItem from "@/components/MaterialPaginationItem.vue";

const router = useRouter();

// üîπ Î°úÍ∑∏Ïù∏Îêú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ (localStorageÏóêÏÑú ÏùΩÏùå)
const currentUserId = ref(null);
const rawAuthCode = ref(""); // AA1~AA4
const selectedRole = ref(2); // 1~4 Îß§ÌïëÎêú Ïà´Ïûê Ïó≠Ìï†

const list = ref([]);
const loading = ref(false);
const error = ref(null);

// üîç Í≤ÄÏÉâ / ÌïÑÌÑ∞ / Ï†ïÎ†¨ ÏÉÅÌÉú
const searchText = ref(""); // Ïù∏ÌíãÏóê Î¨∂Ïó¨ÏûàÎäî Í∞í
const appliedSearch = ref(""); // Ïã§Ï†ú ÌïÑÌÑ∞Ïóê Ïì∞Îäî Í∞í(Í≤ÄÏÉâ Î≤ÑÌäº ÎàåÎ†ÄÏùÑ ÎïåÎßå Í∞±Ïã†)
const statusFilter = ref("ALL"); // ALL | CB1~CB6
const sortOption = ref("COUNSEL_RECENT");

// placeholder (ÏãúÏä§ÌÖúÏù¥Î©¥ Í∏∞Í¥ÄÎ™Ö Ìè¨Ìï®)
const searchPlaceholder = computed(() => {
  if (selectedRole.value === 4) {
    return "ÏßÄÏõêÏûê, Î≥¥Ìò∏Ïûê, Îã¥ÎãπÏûê, Í∏∞Í¥ÄÎ™Ö Í≤ÄÏÉâ";
  }
  return "ÏßÄÏõêÏûê, Î≥¥Ìò∏Ïûê, Îã¥ÎãπÏûê Í≤ÄÏÉâ";
});

// üîπ ÌéòÏù¥Ïßï ÏÉÅÌÉú
const currentPage = ref(1);
const pageSize = 10;

// üîª Î∞òÎ†§ ÏÇ¨Ïú† Î™®Îã¨ ÏÉÅÌÉú
const rejectReasonModalOpen = ref(false);
const rejectReasonText = ref("");
const rejectReasonLoading = ref(false);
const rejectReasonError = ref("");
const rejectReasonDate = ref("");

// Ïó≠Ìï† ÏΩîÎìú ‚Üí Ïà´Ïûê Ïó≠Ìï† Îß§Ìïë
function mapAuthToRole(code) {
  switch (code) {
    case "AA1":
      return 1;
    case "AA2":
      return 2;
    case "AA3":
      return 3;
    case "AA4":
      return 4;
    default:
      return 1;
  }
}

// üîπ Îã¥ÎãπÏûê Ïó≠Ìï† Ïó¨Î∂Ä (2Ïù¥Î©¥ true)
const isAssigneeRole = computed(() => Number(selectedRole.value) === 2);

// üîπ status Ï†ïÍ∑úÌôî
function normStatus(raw) {
  return (raw ?? "").toString().trim().toUpperCase();
}

// ÏûÑÏãú ÏÉÅÌÉú: CB1, CB2 Îëò Îã§ "ÏÉÅÎã¥ Ï†Ñ"ÏúºÎ°ú Ï≤òÎ¶¨
function isTempStatus(code) {
  const s = normStatus(code);
  return s === "CB1" || s === "CB2";
}

function formatDate(val) {
  return val?.slice?.(0, 10) || "-";
}

function statusLabel(code) {
  switch (normStatus(code)) {
    case "CB1":
      return "ÏÉÅÎã¥Ï†Ñ";
    case "CB2":
      return "ÏÉÅÎã¥Ï†Ñ";
    case "CB3":
      return "Í≤ÄÌÜ†Ï†Ñ";
    case "CB4":
      return "Î∞òÎ†§";
    case "CB5":
      return "Í≤ÄÌÜ†ÏôÑÎ£å";
    case "CB6":
      return "Ïû¨ÏäπÏù∏ÏöîÏ≤≠";
    default:
      return code || "-";
  }
}

// üîπ Î∞∞ÏßÄ ÏÉâÏÉÅ ÌÅ¥ÎûòÏä§
function statusPillClass(code) {
  switch (normStatus(code)) {
    case "CB1":
    case "CB2":
      return "status-pill--before";
    case "CB3":
      return "status-pill--review";
    case "CB5":
      return "status-pill--done";
    case "CB6":
      return "status-pill--resubmit";
    default:
      return "status-pill--default";
  }
}

// üîç Í≤ÄÏÉâ/ÌïÑÌÑ∞/Ï†ïÎ†¨Ïù¥ Î™®Îëê Ï†ÅÏö©Îêú Î¶¨Ïä§Ìä∏
const filteredList = computed(() => {
  let rows = [...list.value];

  // 1) Í≤ÄÏÉâ (ÏßÄÏõêÏûê / Î≥¥Ìò∏Ïûê / Îã¥ÎãπÏûê / [ÏãúÏä§ÌÖúÏù¥Î©¥ Í∏∞Í¥ÄÎ™Ö])
  const q = appliedSearch.value.trim().toLowerCase();
  if (q) {
    rows = rows.filter((row) => {
      const baseTargets = [
        row.child_name, // ÏßÄÏõêÏûê
        row.writer_name, // Î≥¥Ìò∏Ïûê
        row.assi_name, // Îã¥ÎãπÏûê
      ];
      const extraTargets =
        selectedRole.value === 4
          ? [row.org_name] // ÏãúÏä§ÌÖú Í∂åÌïúÏùº Îïå Í∏∞Í¥ÄÎ™Ö
          : [];

      const targets = [...baseTargets, ...extraTargets];

      return targets.some((v) =>
        String(v || "")
          .toLowerCase()
          .includes(q)
      );
    });
  }

  // 2) ÏÉÅÌÉú ÌïÑÌÑ∞
  if (statusFilter.value !== "ALL") {
    if (statusFilter.value === "TEMP") {
      // ÏÉÅÎã¥Ï†Ñ(CB1, CB2) Î™®Îëê Ìè¨Ìï®
      rows = rows.filter((row) => isTempStatus(row.status));
    } else {
      rows = rows.filter(
        (row) => normStatus(row.status) === statusFilter.value
      );
    }
  }
  // 3) Ï†ïÎ†¨
  if (sortOption.value === "COUNSEL_RECENT") {
    // ÏÉÅÎã¥Ïùº ÏµúÏã†Ïàú (note_created_at DESC)
    rows.sort((a, b) => {
      const aDate = a.note_created_at ?? "";
      const bDate = b.note_created_at ?? "";

      if (aDate && bDate && aDate !== bDate) {
        return bDate.localeCompare(aDate);
      }

      // ÏÉÅÎã¥ÏùºÏù¥ ÏóÜÍ±∞ÎÇò Í∞ôÏúºÎ©¥ Ï†úÏ∂úÏùº Í∏∞Ï§Ä Î≥¥Ï°∞ Ï†ïÎ†¨
      return (b.submit_at || "").localeCompare(a.submit_at || "");
    });
  } else if (sortOption.value === "COUNSEL_OLD") {
    // ÏÉÅÎã¥Ïùº Ïò§ÎûòÎêúÏàú (note_created_at ASC)
    rows.sort((a, b) => {
      const aDate = a.note_created_at ?? "";
      const bDate = b.note_created_at ?? "";

      if (aDate && bDate && aDate !== bDate) {
        return aDate.localeCompare(bDate);
      }

      return (a.submit_at || "").localeCompare(b.submit_at || "");
    });
  } else if (sortOption.value === "SUBMIT_RECENT") {
    // Ï†úÏ∂úÏùº ÏµúÏã†Ïàú (submit_at DESC)
    rows.sort((a, b) => {
      return (b.submit_at || "").localeCompare(a.submit_at || "");
    });
  } else if (sortOption.value === "SUBMIT_OLD") {
    // Ï†úÏ∂úÏùº Ïò§ÎûòÎêúÏàú (submit_at ASC)
    rows.sort((a, b) => {
      return (a.submit_at || "").localeCompare(b.submit_at || "");
    });
  } else if (sortOption.value === "NAME") {
    // Ïù¥Î¶ÑÏàú: child_name Í∏∞Ï§Ä, ÏóÜÏúºÎ©¥ "Î≥∏Ïù∏"
    rows.sort((a, b) => {
      const an = a.child_name || "Î≥∏Ïù∏";
      const bn = b.child_name || "Î≥∏Ïù∏";
      return an.localeCompare(bn, "ko");
    });
  }
  return rows;
});

// üîπ ÌéòÏù¥ÏßÄ Ïàò & ÌéòÏù¥ÏßïÎêú Î¶¨Ïä§Ìä∏
const totalPages = computed(() =>
  Math.max(1, Math.ceil(filteredList.value.length / pageSize) || 1)
);

const paginatedList = computed(() => {
  const start = (currentPage.value - 1) * pageSize;
  return filteredList.value.slice(start, start + pageSize);
});

function changePage(page) {
  if (page < 1 || page > totalPages.value) return;
  currentPage.value = page;
}

// üîç Í≤ÄÏÉâ Î≤ÑÌäº / ÏóîÌÑ∞ ÎàåÎ†ÄÏùÑ Îïå
function onSearch() {
  appliedSearch.value = searchText.value;
  currentPage.value = 1;
}

// üîπ ÏÉÅÎã¥ Î™©Î°ù Ï°∞Ìöå
async function fetchList() {
  loading.value = true;
  error.value = null;

  try {
    const params = {
      role: selectedRole.value,
      userId: currentUserId.value,
    };

    const { data } = await axios.get("/api/counsel", { params });
    list.value = Array.isArray(data?.result) ? data.result : [];
    currentPage.value = 1; // Î™©Î°ù Îã§Ïãú Î∂àÎü¨Ïò¨ Îïå Ï≤´ ÌéòÏù¥ÏßÄÎ°ú
  } catch (e) {
    console.error(e);
    error.value = e.message || "ÏÉÅÎã¥ Î™©Î°ù Ï°∞Ìöå Ï§ë Ïò§Î•ò";
    list.value = [];
  } finally {
    loading.value = false;
  }
}

// üîπ Î∞òÎ†§ ÏÇ¨Ïú† Î™®Îã¨ Ïó¥Í∏∞
async function openRejectReason(row) {
  rejectReasonModalOpen.value = true;
  rejectReasonText.value = "";
  rejectReasonDate.value = "";
  rejectReasonError.value = "";
  rejectReasonLoading.value = true;

  try {
    const submitCode = Number(row.submit_code);
    if (!submitCode) {
      throw new Error("Ïú†Ìö®Ìïú Ï†úÏ∂úÎ≤àÌò∏Í∞Ä ÏóÜÏäµÎãàÎã§. (submit_code ÏóÜÏùå)");
    }

    const { data } = await axios.get(
      `/api/counsel/${submitCode}/rejection-reason`
    );

    if (data?.success === false) {
      throw new Error(data.message || "Î∞òÎ†§ ÏÇ¨Ïú†Î•º Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
    }

    const r = data.result || data;

    rejectReasonText.value = r.rejection_reason ?? data.rejection_reason ?? "";
    rejectReasonDate.value = r.rejection_date ?? data.rejection_date ?? "";
  } catch (e) {
    console.error(e);
    rejectReasonError.value =
      e.response?.data?.message || e.message || "Î∞òÎ†§ ÏÇ¨Ïú† Ï°∞Ìöå Ï§ë Ïò§Î•ò";
  } finally {
    rejectReasonLoading.value = false;
  }
}

function closeRejectReasonModal() {
  rejectReasonModalOpen.value = false;
}

// ÎùºÏö∞ÌåÖ
function goWrite(row) {
  router.push({
    name: "counsel-new",
    params: { submitCode: row.submit_code },
  });
}

function goEdit(row) {
  router.push({
    name: "counsel-edit",
    params: { submitCode: row.submit_code },
  });
}

function goDetail(row) {
  router.push({
    name: "counsel-detail",
    params: { submitCode: row.submit_code },
    query: { role: selectedRole.value },
  });
}

// üîπ ÎßàÏö¥Ìä∏ Ïãú localStorageÏóêÏÑú ÏÇ¨Ïö©Ïûê/Ïó≠Ìï† Ï†ïÎ≥¥ ÏùΩÍ∏∞
onMounted(() => {
  try {
    const stored = localStorage.getItem("user");

    if (stored) {
      const u = JSON.parse(stored);

      const userCode = u.user_code ?? null;
      const auth = u.role ?? "AA1";

      currentUserId.value = userCode ? Number(userCode) : null;
      rawAuthCode.value = String(auth).toUpperCase();
      selectedRole.value = mapAuthToRole(rawAuthCode.value);
    } else {
      // Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Í∞Ä ÏóÜÏùÑ Îïå Í∏∞Î≥∏ (ÏùºÎ∞ò ÏÇ¨Ïö©Ïûê)
      currentUserId.value = null;
      rawAuthCode.value = "AA1";
      selectedRole.value = mapAuthToRole("AA1");
    }
  } catch (e) {
    console.error("localStorage user ÌååÏã± Ïò§Î•ò:", e);
    currentUserId.value = null;
    rawAuthCode.value = "AA1";
    selectedRole.value = mapAuthToRole("AA1");
  }

  fetchList();
});
</script>

<style scoped>
section {
  color: #111827;
  font-size: 15px; /* Ï†ÑÏ≤¥ Í∏∞Î≥∏ Ìè∞Ìä∏ ÌÅ¨Í∏∞ */
}

/* ÌéòÏù¥ÏßÄ Ï†ÑÏ≤¥ Ìè≠ Ïª®ÌÖåÏù¥ÎÑà: Ìó§Îçî + Ïπ¥Îìú Ìè≠ ÌÜµÏùº */
.page-shell {
  max-width: 1600px;
  margin: 0 auto;
}

/* Ìó§Îçî Ìïú Ï§Ñ Ïú†ÏßÄ */
.header-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  gap: 1rem;
}

/* ÏôºÏ™Ω Ï†úÎ™© */
.header-title {
  flex: 1 1 auto;
  min-width: 0;
}

/* Ïò§Î•∏Ï™Ω Ïó≠Ìï† ÌëúÏãú ÏòÅÏó≠ */
.header-action {
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.25rem;
  white-space: nowrap;
}

.role-pill {
  display: inline-flex;
  align-items: center;
